import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserBuyConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        var userData = [{
                name: 'A',
                orders: 10,
                views: 50
            },
            {
                name: 'B',
                orders: 8,
                views: 40
            },
            {
                name: 'C',
                orders: 15,
                views: 30
            },
            {
                name: 'D',
                orders: 5,
                views: 60
            },
            {
                name: 'E',
                orders: 12,
                views: 45
            }
        ];

        // 
        var orderByOrders = userData.slice().sort(function (a, b) {
            return b.orders - a.orders;
        });

        // 
        var orderByViews = userData.slice().sort(function (a, b) {
            return b.views - a.views;
        });



        // 
        function getUserNames(users) {
            return users.map(function (user) {
                return user.name;
            });
        }

        // 
        function getUserOrders(users) {
            return users.map(function (user) {
                return user.orders;
            });
        }

        // 
        function getUserViews(users) {
            return users.map(function (user) {
                return user.views;
            });
        }



        // 
        var option = {
            title: {
                text: '',
                left: 'center'
            },
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                    type: 'shadow'
                }
            },
            legend: {
                data: ['', ''],
                top: 'bottom'
            },
            xAxis: {
                type: 'category',
                data: getUserNames(orderByOrders)
            },
            yAxis: {
                type: 'value'
            },
            series: [{
                    name: '',
                    type: 'bar',
                    data: getUserOrders(orderByOrders)
                },
                {
                    name: '',
                    type: 'bar',
                    data: getUserViews(orderByViews)
                }
            ]
        };

        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setuserBuyConfig