import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'
import * as echarts from 'echarts'

export function setfrmDataConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        const data = [{
                name: '1',
                value: [10, 8]
            },
            {
                name: '2',
                value: [-5, 12]
            },
            {
                name: '3',
                value: [-15, -6]
            },
            {
                name: '4',
                value: [8, -10]
            },
            // ...
        ];

        // 
        const option = {
            title: {
                text: '',
                x: 'center',
                textStyle: {
                    fontWeight: 'bold',
                    fontSize: 16,
                },
            },
            grid: {
                left: '10%',
                right: '10%',
                top: '15%',
                bottom: '10%',
                containLabel: true,
            },
            xAxis: {
                type: 'value',
                name: 'X',
                scale: true,
                axisLabel: {
                    formatter: '{value}',
                },
            },
            yAxis: {
                type: 'value',
                name: 'Y',
                scale: true,
                axisLabel: {
                    formatter: '{value}',
                },
            },
            series: [{
                type: 'scatter',
                data: data,
                symbolSize: function (data) {
                    return Math.sqrt(data[2]) * 10; // 
                },
                label: {
                    show: true,
                    formatter: function (param) {
                        return param.data.name; // 
                    },
                    position: 'top',
                },
                itemStyle: {
                    color: '#428bca', // 
                },
            }, ],
        };





        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setfrmDataConfig